import React from "react";
import { Select } from "antd";

export default function PeriodSelectBox(props) {
  return (
    <Select {...props}>
      <Select.Option value={null}>{null}</Select.Option>
      <Select.Option value="30">30 Seconds</Select.Option>
      <Select.Option value="60">1 Minute</Select.Option>
      <Select.Option value="600">10 Minutes</Select.Option>
      <Select.Option value="1800">30 Minutes</Select.Option>
      <Select.Option value="3600">1 Hour</Select.Option>
      <Select.Option value="21600">6 Hours</Select.Option>
      <Select.Option value="43200">12 Hours</Select.Option>
      <Select.Option value="86400">1 Day</Select.Option>
    </Select>
  );
}
